import React from 'react'

import Layout from "../components/layout"
import SEO from "../components/seo"

const portafolio = () => (
    <Layout>
      <SEO title="portafolio" keywords={[`gatsby`, `application`, `react`]} />
      <h1>Portafolio</h1>
    </Layout>
  )
  
  export default portafolio